.diana {
    font-size: 25px;
    font-family: 'Bangers', cursive;
    font-weight: 600;
    margin:10px;
}
.diana a{
    text-decoration: none;
    color:goldenrod;

}
.faq{
    font-size: 23px;
    display: flex;
}

.listPages{
    margin-left: 100px;
    background: black;
    width: 150px;
    height: min-content;
    list-style: none;
    padding: 8px;
    border: 1px solid black;
}

.listPages a{
    display: block;
    color: mediumturquoise;
    text-decoration: none;
    font-size: 19px;
    font-weight: unset;
    list-style: none;
    margin-left: 6px;
    margin-bottom: 10px;
    transition: color .1s linear;
}
.flex{
    display:flex;
}
.backHome{
    margin-top: 15px;
    font-size: 18px;
}
.backHome a {
    text-decoration: none;
    color:goldenrod;
}