
.header {
    height: 60px;
    position: relative;
}
.header img {
    height: 100%;
    width: 100%;
}
.name {
    color: greenyellow;
    position: absolute;
    right: 45px;
    top: 20px;
}

.headerClock {
    left: 15px;
    color: mediumturquoise;
    top: -15px;
    position: absolute;
}

.logout {
    color: red;
    font-size: 12px;
    right: -35px;
    top: -14px;
    position: absolute;
    transition: color .2s linear;
}

.logout:hover {
    color: gold;
    cursor: pointer;
}

.login {
    color: white;
    font-size: 17px;
    text-decoration: none;
}

.login:hover {
    color: gold;
}
.avatar{
    width: 40px;
    height: 40px;
    border-radius:30px;
    position: absolute;
    right: 120px;
    top: 10px;
}